.title {
  font-size: 32px;
}

.password-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.generator-card {
  margin: 12px 0;
  padding: 24px;
  width: 500px;
  border: 1px solid #000;
  border-radius: 8px;
}

.password-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.password-field {
  /* min-width: 200px; */
  flex: 7.5;
  vertical-align: middle;
  border-bottom: 1px solid #000;
  min-height: 40px;
  /* border-radius: 6px; */
  font-size: 20px;
  padding: 2px 8px;
  display: flex;
  align-items: center;
}

.password-generator {
  flex: 2.5;
  text-align: right;
}

.password-generator button {
  padding: 11px 16px;
  background-color: #fff;
  color: #000;
  outline: none;
  border: 1px solid #000;
  border-radius: 6px;
  cursor: pointer;
}

.password-generator button:hover {
  background-color: #000000;
  /* opacity: 0.9; */
  color: #fff;
  border: 1px solid #fff;
}

.options-section {
  font-size: 20px;
  padding: 24px 6px;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.password-length-input {
  margin-left: 12px;
  border: none;
  border-bottom: 1px solid #000;
  padding: 4px 8px;
}

.password-length-input:focus {
  outline: none;
}

.password-options-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-size: 16px;
}

.copy-password-container {
  margin: 12px 0;
  padding: 24px;
  width: 500px;
}

.copy-password-button {
  width: 100%;
  padding: 12px;
  border-radius: 8px;

  font-size: 20px;
  border: 1px solid transparent;
  border-bottom: 1px solid #000;
  outline: none;
  cursor: pointer;

  transition: 0.3s ease-in-out;
}

.copy-password-button:hover {
  color: whitesmoke;
  background-color: #000;
}
.copy-password-button:active {
  opacity: 0.6;
}
